
"use client";

import type { Engine } from "tsparticles-engine";
import type { ISourceOptions } from "tsparticles-engine";
import Particles, { initParticlesEngine } from "react-tsparticles";
import { loadSlim } from "tsparticles-slim"; // loads tsparticles-engine and slim preset
import { useEffect, useMemo, useState } from "react";
import { useThemeContext } from "@/contexts/ThemeContext";

interface ProjectCardParticlesProps {
  className?: string;
}

const ProjectCardParticles = ({ className }: ProjectCardParticlesProps) => {
  const [init, setInit] = useState(false);
  const { theme } = useThemeContext();

  // this useEffectstück initializes the tsParticles engine (only once per page)
  useEffect(() => {
    initParticlesEngine(async (engine: Engine) => {
      await loadSlim(engine);
    }).then(() => {
      setInit(true);
    });
  }, []);

  const particleOptions = useMemo((): ISourceOptions => {
    // Get current theme foreground color for particles
    // This will be based on the theme when the component mounts or theme state changes
    let particleColor = "#ffffff"; // Default for dark theme
    if (typeof document !== 'undefined') {
        const rootStyle = getComputedStyle(document.documentElement);
        const fgColorHSL = rootStyle.getPropertyValue('--foreground').trim();
        if (fgColorHSL) {
            // Assuming HSL format like "210 20% 95%"
            const [h, s, l] = fgColorHSL.split(' ').map(parseFloat);
            if (!isNaN(h) && !isNaN(s) && !isNaN(l)) {
                const tempColor = new THREE.Color(); // Using THREE.Color to convert HSL, though it's a bit heavy here.
                                                  // A lighter HSL-to-Hex converter could be used if THREE is not already a common dep.
                tempColor.setHSL(h / 360, s / 100, l / 100);
                particleColor = `#${tempColor.getHexString()}`;
            }
        }
    }


    return {
      fpsLimit: 60,
      fullScreen: { enable: false }, // Particles stay within their container
      particles: {
        number: {
          value: 30, // Fewer particles for a subtle "ray" effect per card
          density: {
            enable: true,
            value_area: 600,
          },
        },
        color: {
          value: particleColor, // Use themed foreground color
        },
        shape: {
          type: "line", // Lines to simulate rays
        },
        opacity: {
          value: 0.4, // Slightly more visible
          random: true,
          animation: { // Renamed from anim
            enable: true,
            speed: 0.8,
            minimumValue: 0.1, // Renamed from opacity_min
            sync: false,
          },
        },
        size: {
          value: {min: 1, max: 3}, // Lines can have a bit of thickness variation
          random: true,
          animation: { // Renamed from anim
            enable: false,
          },
        },
        links: { // Renamed from line_linked
            enable: false, // No lines connecting particles for "rays"
        },
        move: {
          enable: true,
          speed: 1.5,
          direction: "top-right", // Rays moving diagonally
          random: true,
          straight: true,
          outModes: { // Renamed from out_mode and accepts object
            default: "out",
          },
          bounce: false,
        },
      },
      interactivity: {
        detectsOn: "canvas", // Renamed from detect_on
        events: {
          onClick: { // Renamed from onclick
            enable: false,
          },
          onHover: { // Renamed from onhover
            enable: false,
          },
          resize: { enable: true } // Allow particles to adapt to container resize
        },
      },
      detectRetina: true,
    };
  }, [theme]); // Recompute options if theme changes to update particleColor

  if (init) {
    return (
      <Particles
        id={`tsparticles-card-${Math.random().toString(36).substring(7)}`} // Unique ID for each instance
        className={className}
        options={particleOptions}
      />
    );
  }

  return <div className={className}></div>; // Fallback or loading state
};

// Helper for THREE.Color - this is a bit heavy just for color conversion.
// In a real app, if THREE is not used elsewhere extensively, find a lighter hsl to hex converter.
// For now, assuming THREE might be used elsewhere or this is acceptable.
// If not, this import can be removed if a simpler HSL to HEX is used.
import * as THREE from 'three';

export default ProjectCardParticles;
