
"use client";

import { useState, useRef, useEffect, type FormEvent, useCallback } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Sheet, SheetContent, SheetHeader, SheetTitle, SheetFooter, SheetTrigger } from '@/components/ui/sheet';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Bot, Send, UserCircle } from 'lucide-react';
// askDvizioonAgent is no longer called directly from here
import { checkAgentLiveness, type AgentStatusOutput } from '@/ai/flows/agent-status-flow';
import { useTranslations } from '@/hooks/useTranslations';
import Image from 'next/image';
import ReactMarkdown from 'react-markdown'; // Ensure this is imported
import { useLanguageContext } from '@/contexts/LanguageContext';


interface Message {
  id: string;
  sender: 'user' | 'agent';
  text: string;
  isLoading?: boolean;
}

interface ChatWidgetProps {
  isOpen: boolean;
  onOpenChange: (isOpen: boolean) => void;
  messages: Message[]; // Receive messages as prop
  onSendMessage: (messageText: string) => void; // Receive handler to send messages
  initialMessage?: string; // Prop for initial message (e.g., from project card)
}

export default function ChatWidget({ 
  isOpen, 
  onOpenChange, 
  messages,         // Use messages from props
  onSendMessage,    // Use onSendMessage from props
  initialMessage    // Use initialMessage from props
}: ChatWidgetProps) {
  const t = useTranslations();
  // const { locale } = useLanguageContext(); // locale is used in PortfolioClientPage before calling AI
  const [inputValue, setInputValue] = useState('');
  const scrollAreaRef = useRef<HTMLDivElement>(null);
  const [isAgentTyping, setIsAgentTyping] = useState(false); // This might need coordination with parent if parent handles loading state
  
  const [agentStatus, setAgentStatus] = useState<'online' | 'offline' | 'connecting'>('connecting');
  const [agentVersion] = useState('1.0'); 
  const statusCheckedRef = useRef(false);
  const initialGreetingSentRef = useRef(false);
  const initialMessageSentRef = useRef(false); // Ref to track if prop initialMessage was sent

  const scrollToBottom = useCallback(() => {
    if (scrollAreaRef.current) {
      const scrollViewport = scrollAreaRef.current.querySelector('div[data-radix-scroll-area-viewport]');
      if(scrollViewport) {
        scrollViewport.scrollTop = scrollViewport.scrollHeight;
      }
    }
  }, []);

  // Agent status check effect
  useEffect(() => {
    const performAgentStatusCheck = async () => {
      setAgentStatus('connecting');
      try {
        const liveness: AgentStatusOutput = await checkAgentLiveness();
        if (liveness.status === 'ok') {
          setAgentStatus('online');
        } else {
          setAgentStatus('offline');
        }
      } catch (error) {
        console.error("Agent liveness check failed:", error);
        setAgentStatus('offline');
      }
    };

    if (isOpen && !statusCheckedRef.current) { 
      performAgentStatusCheck();
      statusCheckedRef.current = true;
    }
    if (!isOpen) { 
        statusCheckedRef.current = false;
        initialGreetingSentRef.current = false;
        initialMessageSentRef.current = false; // Reset when chat closes
    }
  }, [isOpen]);

  // Effect to handle initial greeting if chat opens plainly (no initialMessage from prop)
   useEffect(() => {
    if (isOpen && !initialMessage && messages.length === 0 && !initialGreetingSentRef.current && t.chatWidget?.initialGreeting) {
      // onSendMessage will handle adding this to PortfolioClientPage's state
      // For now, we are letting PortfolioClientPage handle adding agent's first message
      // This could be: onSendMessage(t.chatWidget.initialGreeting, 'agent_greeting');
      // But to keep it simple, if no initialMessage, the parent will send the first greeting if desired.
      // This useEffect might be better placed in the parent or the parent sends a specific "greeting" type message.
      // For now, this is simplified as the parent (PortfolioClientPage) will manage adding the first messages.
      // If we want the widget to *itself* send an initial greeting, it would call onSendMessage
      // but that message wouldn't be user input.
      // Let's assume PortfolioClientPage adds the initial greeting to chatMessages if needed.
      // For now, if no initialMessage, we just show the empty chat or existing messages.
      initialGreetingSentRef.current = true; // Mark as handled, even if no message is sent from here
    }
  }, [isOpen, initialMessage, messages.length, t.chatWidget?.initialGreeting, onSendMessage]);


  const handleFormSubmit = async (e?: FormEvent<HTMLFormElement>) => {
    if (e) e.preventDefault();
    const userMessageText = inputValue.trim();
    if (!userMessageText) return;

    if (agentStatus === 'offline') {
        // If agent is offline, we can add a message directly here or let onSendMessage handle it.
        // For simplicity, let's assume onSendMessage in parent handles this by checking status before calling AI.
        // The parent will add an "agent offline" message to its `chatMessages` state.
        // For now, onSendMessage will be called, and parent can decide.
        // This is just to make the button less active.
         alert(t.chatWidget.agentOfflineError || "Agent is offline. Please try again later.");
         return;
    }
    
    onSendMessage(userMessageText); // Call parent handler
    setInputValue(''); 
    // setIsAgentTyping(true); // Parent should manage this based on the async call in onSendMessage
  };
  
  // Simulate agent typing based on messages (if last message is loading)
  useEffect(() => {
    if (messages.length > 0) {
      const lastMessage = messages[messages.length - 1];
      if (lastMessage.sender === 'agent' && lastMessage.isLoading) {
        setIsAgentTyping(true);
      } else {
        setIsAgentTyping(false);
      }
    } else {
      setIsAgentTyping(false);
    }
  }, [messages]);


  useEffect(() => {
    scrollToBottom();
  }, [messages, scrollToBottom]);
  

  return (
    <>
      <Sheet open={isOpen} onOpenChange={onOpenChange}>
        <SheetTrigger asChild>
          <Button
            variant="default"
            className="chat-widget-button fixed bottom-6 right-6 h-16 w-16 rounded-full shadow-lg z-50 p-0 group
                        bg-primary hover:bg-primary/90 text-primary-foreground
                        focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2
                        transform transition-all hover:scale-105 active:scale-95 overflow-visible"
            aria-label={t.chatWidget.openChatLabel}
          >
            <Image
              src="/dvizioon-logo-avatar.png"
              alt={t.header.logoAlt}
              width={60} 
              height={60} 
              className="object-contain transition-transform duration-300 group-hover:scale-110 rounded-full"
            />
          </Button>
        </SheetTrigger>
        <SheetContent side="right" className="w-[400px] sm:w-[540px] flex flex-col p-0">
          <SheetHeader className="p-4 border-b border-border space-y-1.5">
            <div className="flex items-center gap-2 justify-center sm:justify-start">
              <Bot className="h-6 w-6 text-primary" />
              <SheetTitle className="text-lg">{t.chatWidget.title}</SheetTitle>
            </div>
            <div className="text-xs text-muted-foreground flex flex-col items-center sm:items-end sm:flex-row sm:justify-end gap-x-1.5 gap-y-0.5 pt-0.5">
                <div className="flex items-center gap-1">
                    <span className={`h-2 w-2 rounded-full inline-block ${
                        agentStatus === 'online' ? 'bg-green-500' :
                        agentStatus === 'offline' ? 'bg-red-500' :
                        'bg-yellow-500 animate-pulse'
                    }`}></span>
                    <span className="capitalize">
                        {
                        agentStatus === 'online' ? t.chatWidget.statusOnline :
                        agentStatus === 'offline' ? t.chatWidget.statusOffline :
                        t.chatWidget.statusConnecting
                        }
                    </span>
                </div>
                <span className="hidden sm:inline-block mx-0.5 text-muted-foreground/50">|</span>
                <span>{t.chatWidget.agentVersionLabel} {agentVersion}</span>
            </div>
          </SheetHeader>
          <ScrollArea className="flex-grow p-4" ref={scrollAreaRef}>
            <div className="space-y-4">
              {messages.map((msg) => (
                <div
                  key={msg.id}
                  className={`chat-message flex items-end gap-2 ${
                    msg.sender === 'user' ? 'user justify-end' : 'agent justify-start'
                  }`}
                >
                  {msg.sender === 'agent' && <Bot className="h-6 w-6 text-primary self-start shrink-0" />}
                  <div
                    className={`p-3 rounded-lg max-w-[85%] break-words
                      ${msg.sender === 'user' ? 'bg-primary text-primary-foreground rounded-br-none'
                                              : 'bg-card text-card-foreground rounded-bl-none border border-border/50'}
                      ${msg.isLoading ? 'animate-pulse' : ''}
                    `}
                  >
                    {/* --- START: REACT-MARKDOWN RENDERING (Enabled) --- */}
                    {/* Ensure react-markdown is installed: npm install react-markdown */}
                    {/* If you still get "Module not found" after install, ensure server is restarted. */}
                    {msg.sender === 'agent' && !msg.isLoading ? (
                      <div className="prose prose-sm dark:prose-invert max-w-full prose-p:my-1 prose-ul:my-1 prose-ol:my-1">
                        <ReactMarkdown
                          components={{
                            // eslint-disable-next-line @typescript-eslint/no-unused-vars
                            a: ({node, ...props}) => <a {...props} target="_blank" rel="noopener noreferrer" className="text-primary hover:underline" />
                          }}
                        >
                          {msg.text}
                        </ReactMarkdown>
                      </div>
                    ) : (
                      // Fallback for user messages or when agent message is loading
                      msg.text.split('\n').map((line, i) => <p key={i} className="m-0 p-0">{line}</p>)
                    )}
                    {/* --- END: REACT-MARKDOWN RENDERING (Enabled) --- */}

                    {/* --- Plain text rendering (Used if ReactMarkdown fails or is not installed) --- */}
                    {/* {msg.text.split('\n').map((line, i) => <p key={i} className="m-0 p-0">{line}</p>)} */}
                    {/* --- End: Plain text rendering --- */}
                  </div>
                  {msg.sender === 'user' && <UserCircle className="h-6 w-6 text-muted-foreground self-start shrink-0" />}
                </div>
              ))}
            </div>
          </ScrollArea>
          <SheetFooter className="p-4 border-t border-border bg-background">
            <form onSubmit={handleFormSubmit} className="flex w-full items-center space-x-2">
              <Input
                type="text"
                placeholder={t.chatWidget.inputPlaceholder}
                value={inputValue}
                onChange={(e) => setInputValue(e.target.value)}
                className="flex-grow bg-input focus:bg-input/70"
                disabled={isAgentTyping || agentStatus === 'connecting'}
                onKeyDown={(e) => {
                  if (e.key === 'Enter' && !e.shiftKey) {
                    e.preventDefault();
                    handleFormSubmit();
                  }
                }}
              />
              <Button type="submit" size="icon" className="bg-primary hover:bg-primary/90 text-primary-foreground" disabled={isAgentTyping || !inputValue.trim() || agentStatus !== 'online'}>
                <Send className="h-5 w-5" />
                <span className="sr-only">{t.chatWidget.sendButtonLabel}</span>
              </Button>
            </form>
          </SheetFooter>
        </SheetContent>
      </Sheet>
    </>
  );
}

    